<template>
    <div class="container">
        <div class="container-title">活动列表</div>
        <!-- <div class="search-box">
            <div class="search-item">
                <div class="search-item-title">活动名称</div>
                <el-input class="search-item-inp" size="small" v-model="input" placeholder=""></el-input>
            </div>
        </div> -->
        <div class="user-btn">
            <el-button class="user-btn-inp" @click="add" type="primary">新增</el-button>
        </div>
        <div class="table-list">
            <el-table ref="singleTable" :data="tableData" style="width: 100%">
                <el-table-column property="id" label="id">
                </el-table-column>
                <el-table-column property="title" label="活动标题">
                </el-table-column>
                <el-table-column fixed="right" label="操作" width="120">
                    <template slot-scope="scope">
                        <el-button @click.native.prevent="previewDuty(scope.row)" type="text" size="small">
                            预览
                        </el-button>
                        <el-button @click.native.prevent="editDuty(scope.row)" type="text" size="small">
                            编辑
                        </el-button>
                        <el-button @click.native.prevent="deleteDuty(scope.row)" type="text" size="small">
                            删除
                        </el-button>
                    </template>
                </el-table-column>
            </el-table>
            <div class="pagination">
                <el-pagination :current-page="form.pageNumber" layout="prev, pager, next,jumper" :total="total" @current-change="currentChange" :page-size="form.pageSize">
                </el-pagination>
            </div>
        </div>
    </div>
</template>


<script>
import { activeList, activeDelete } from '@/api/active';
export default {
    data () {
        return {
            tableData: [
                // { title: '活动的名称' }
            ],
            form: {
                pageNumber: 1,
                pageSize: 10,
                isPaging: true,
            },
            total: 0,
            input: ''
        };
    },
    created () {
        this.getList();
    },
    methods: {
        currentChange (val) {
            this.form.pageNumber = val;
            this.getList();
        },
        getList () {
            activeList(this.form).then(res => {
                if (res.data.code === 0) {
                    this.total = res.data.data.amount;
                    this.tableData = res.data.data.rows.map(item => {
                        return item.activityRow;
                    });
                } else {
                    this.total = 0;
                    this.tableData = [];
                    this.form.pageNumber = 1;
                }
            });
        },
        add () {
            this.$router.push({
                path: '/eventManagement/eventManagementAddOrEdit',
                query: {
                    type: 'add'
                }
            });
        },
        editDuty (row) {
            this.$router.push({
                path: '/eventManagement/eventManagementAddOrEdit',
                query: {
                    type: 'edit',
                    id: row.id
                }
            });
        },
        previewDuty (row) {
            this.$router.push({
                path: '/eventManagement/eventManagementAddOrEdit',
                query: {
                    type: 'preview',
                    id: row.id
                }
            });
        },
        deleteDuty (row) {
            this.$confirm('此操作将永久删除该活动, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                this.activeDelete(row.id);
            }).catch(() => {

            });
        },
        activeDelete (id) {
            activeDelete({ id }).then(res => {
                if (res.data.code == 0) {
                    this.$message.success('删除成功');
                    this.getList();
                } else {
                    this.$message.error(`${res.data.message},${res.data.detail}`);
                }
            });
        }
    }
}
</script>

<style scoped>
.list-img {
    width: 200px;
    cursor: pointer;
}
.previewImg-box {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
}
.previewImg {
    width: 500px;
}
</style>